.top{
	height: px2rem(70);
	.top-wrap{
		width: 100%;
		height:px2rem(70);
		background-color: #fff;
		h1{
			font-size: px2rem(32);
			color: #333;
			line-height: px2rem(70);
			text-align: center;
			font-weight: normal;
		}
		position:relative;
	}
}
.icon-left {
    position: absolute;
    left: px2rem(15);
    top: 50%;
    transform: translateY(-50%);
    width: px2rem(20);
    height: px2rem(40);
    background: url(../images/back.png) no-repeat center center;
    background-size: 100% 100%;
    z-index: 2;
    display: block;
}